@media screen and (min-width:375px) {
    .section-comics-spiderman {
        /* width: 100%; */
        /* height: 509px; */
        background-image: url(/src/images/desktop/Characters/spiderman-jump.mobile.png);
        /* background-repeat: no-repeat; */
        /* background-position: right bottom; */
        background-size: cover;
        background-position: center;
    }

    .container-comics-spiderman {
        /* width: 335px; */
        padding-top: 172px;
        padding-left: 20px;
        padding-right: 20px;
    }

    .comics-text-spiderman {
        font-family: 'Poppins';
        font-size: 14px;
        line-height: 1.3;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.7);
        margin-bottom: 14px;

    }

    .comics-title-spiderman {
        font-family: 'Poppins';
        font-weight: 500;
        font-size: 60px;
        line-height: 1;
        letter-spacing: -0.02em;
        color: #fafafa;
        margin-bottom: 16px;

    }

    .comics-description-spiderman {
        font-family: 'Poppins';
        /* columns: auto; */
        padding-left: 0;
        font-size: 14px;
        line-height: 1.3;
        display: flex;
        align-items: center;
        letter-spacing: 0.02em;
        color: #fafafa;
        padding-bottom: 150px;
    }

    .back-spiderman {
        position: absolute;
        color: rgba(255, 255, 255, 0.7);
        border: 1px solid rgba(250, 250, 250, 0.5);
        border-radius: 1000px;
        bottom: -60px;
        right: 20px;
        padding: 10px 20px;
        height: 36px;
        font-size: 12px;
        line-height: 1.33;
        display: flex;
    }

}

@media screen and (min-width:768px) {
    .section-comics-spiderman {
        background-image: url(/src/images/desktop/Characters/spiderman-jump.tablet.jpg);
        background-color: var(--praimery-bg-color);
        background-size: cover;

        /* height: 780px; */
        padding-top: 0;

    }

    .container-comics-spiderman {
        width: 768px;
        padding-left: 20px;
        padding-top: 212px;
        padding-bottom: 251px;

    }

    .comics-text-spiderman {
        color: rgba(255, 255, 255, 0.7);
        font-size: 18px;
        line-height: 1.6;
        margin-bottom: 14px;

    }

    .comics-title-spiderman {
        font-size: 128px;
        line-height: 1.28;
        margin-bottom: 16px;
        color: #fafafa;

    }

    .comics-description-spiderman {
        font-size: 16px;
        line-height: 1.25;
        color: #fafafa;
        margin-left: 100px;

    }

    .back-spiderman {
        position: absolute;
        right: 32px;
        bottom: -275px;
    }
}

@media screen and (min-width:1440px) {
    .section-comics-spiderman {

        background-image: url(/src/images/desktop/Characters/spider-jump.png);
        background-repeat: no-repeat;
        background-size: cover;





        height: 100vh;
    }

    .container-comics-spiderman {
        min-width: 1440px;
        padding-left: 32px;
        padding-top: 323px;
    }

    .comics-text-spiderman {
        color: rgba(255, 255, 255, 0.7);
        ;
        margin-bottom: 16px;

    }

    .comics-title {
        font-size: 150px;
        line-height: 1.5;
    }

    /* .comics-description {

    }  */

    .back-spiderman {
        position: absolute;
        bottom: -250px;
    }
}









